import React from 'react';
import './style/titleSection.scss';
import './style/prices.scss';
import './style/labels.scss';
import './style/stars.scss';
import './style/sharing.scss';
import './style/features.scss';
import titleSectionData from "./Data/titleSectionData";
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faSchool, faShoppingBag, faStar } from "@fortawesome/free-solid-svg-icons";
import {BrowserView , MobileView} from 'react-device-detect';
import Divider from "../../divider/Divider";
function TitleSection({ data }) {
    const stars = [1, 2, 3, 4, 5];
    const socialMedia = [1,2,3];
    let discountPercent = ((data.price - data.newprice) * 100) / data.price;
    return (
        <div className='title-section'>
            <h1 className='title'>{data.title}</h1>
            <h4 className='subtitle'>{data.subtitle}</h4>
            <div className='star-container'>
                <div className='stars'>{stars.map(() => { return (<FontAwesomeIcon icon={faStar} />) })}{titleSectionData.comments}</div>
                <BrowserView><div><button className='submit-btn'>{titleSectionData.shipping}</button></div></BrowserView>
            </div>
            <button className='price-container-btn'>
                <div className='shopping-icon'><FontAwesomeIcon icon={faShoppingBag} /></div>
                <div>
                    <div className='new-price-container'><span>{titleSectionData.purchase}</span><span className='price-num'>{data.newprice}</span>
                        <span>{titleSectionData.currency}</span></div>
                    <div className='old-price-container'><span className='discount-percent'> {discountPercent}{titleSectionData.discount}</span>
                        <span className='old-price'>{data.price}{titleSectionData.currency}</span></div>
                </div>
            </button>
            <button className='available-btn'> <FontAwesomeIcon icon={faShoppingBag} />{titleSectionData.available}</button>
            <div className="label-info-container">
                <div className="label-info">
                <span className='info'> {data.daynum} </span>
                    {titleSectionData.day}
                    </div>
                <div className="label-info">
                <span className='info'> {data.purchased} </span>
                    {titleSectionData.purchased}</div>
            </div>
            <div className='product-charity'>
                با خرید این محصول از گلدتگ <span style={{color:'#ff7a7a'}}>100 تومان</span> به <span style={{color:'#436fbe'}}>خیریه</span> کمک کرده اید.
            </div>
            <MobileView>
            <div className='sharing-container'>
           <div>{titleSectionData.share}</div>
               <div>
                   <span className="social-media-icon"><FontAwesomeIcon icon={faSchool} /></span>
                   <span className="social-media-icon"><FontAwesomeIcon icon={faSchool} /></span>
                   <span className="social-media-icon"><FontAwesomeIcon icon={faSchool} /></span>
               </div>
           </div>
            </MobileView>
            <div className='features-container'>
                {titleSectionData.items.map((item)=>{
                    return(
                        <div className='feature'>
                            <div>
                                <img src={item.img} />
                                <span>{item.title}</span>
                            </div>
                        </div>
                    )
                })}
           </div>
            <Divider className=''/>
           <BrowserView>
           <div className='sharing-container'>
           <div>{titleSectionData.share}</div>
               <div>
                   {
                       socialMedia.map((item)=>{
                           return(
                               <span className="social-media-icon"><FontAwesomeIcon icon={faSchool} /></span>
                           )
                       })
                   }
               </div>
           </div>
           </BrowserView>
        </div>
    )
}
export default TitleSection;